<template>
  <div></div>
</template>

<script>
  import Polygon from 'ol/geom/Polygon'
  import {parse} from 'utils/Style'
  import FeatureMixin from 'utils/Feature'

  // 
  export default {
    name: 'XdhMapRectangle',
    mixins: [FeatureMixin],
    props: {
      loc: {
        type: Array,
        required: true
      },
      width: {
        type: Number,
        default: 0,
        required: true
      },
      height: {
        type: Number,
        default: 0,
        required: true
      },
      background: {
        type: String,
        default: 'red'
      },
      strokeWidth: {
        type: Number,
        default: 0
      },
      strokeColor: {
        type: String,
        default: ''
      },
      zIndex: {
        type: Number
      }
    },
    computed: {
      coordinate() {
        // 
        return this.loc.map(n => Number.parseFloat(n))
      },
      coordinates() {
        const loc = this.coordinate
        return [
          loc,
          [loc[0] + this.width, loc[1]],
          [loc[0] + this.width, loc[1] - this.height],
          [loc[0], loc[1] - this.height],
          loc
        ]
      }
    },
    methods: {
      createStyle() {
        return parse({
          className: 'Style',
          fill: {
            className: 'Fill',
            color: this.background
          },
          stroke: {
            className: 'Stroke',
            width: this.strokeWidth,
            color: this.strokeColor
          },
          text: {
            className: 'Text',
            text: ''
          },
          zIndex: this.zIndex
        })
      },
      createGeometry() {
        return new Polygon([this.coordinates])
      }
    }
  }
</script>
