<template>
  <div class="xdh-map-text hidden"></div>
</template>

<script>
  import {parse} from 'utils/Style'
  import FeatureMixin from 'utils/Feature'
  import Point from 'ol/geom/Point'

  export default {
    name: 'XdhMapText',
    mixins: [FeatureMixin],
    props: {
      // 
      loc: {
        type: Array,
        required: true
      },
      // 
      content: {
        type: String
      },
      // 
      font: {
        type: String,
        default: '14px'
      },
      // 
      color: {
        type: String,
        default: '#333'
      },
      // 
      background: {
        type: String,
        default: 'transparent'
      },
      // 
      border: {
        type: String,
        default: 'transparent'
      },
      // 
      padding: {
        type: Array,
        default() {
          return [2, 2, 2, 2]
        }
      },
      // 
      offset: {
        type: Array,
        default() {
          return [0, 0]
        }
      }
    },
    computed: {
      fullFont() {
        return `${this.font} sans-serif`
      },
      coordinate() {
        // 
        return this.loc.map(n => Number.parseFloat(n))
      }
    },
    watch: {
      /**
       * 
       */
      content() {
        const style = this.getStyle()
        this.feature && this.feature.setStyle(style)
      }
    },
    methods: {
      createStyle() {
        return parse({
          className: 'Style',
          text: {
            className: 'Text',
            text: this.content,
            font: this.fullFont,
            fill: {
              className: 'Fill',
              color: this.color
            },
            backgroundFill: {
              className: 'Fill',
              color: this.background
            },
            backgroundStroke: {
              className: 'Stroke',
              color: this.border
            },
            padding: this.padding,
            offsetX: this.offset[0],
            offsetY: this.offset[1]
          }
        })
      },
      createGeometry() {
        return new Point(this.coordinate)
      }

    }
  }
</script>
